// src/app/page.tsx
'use client';
import { Parallax, ParallaxLayer } from '@react-spring/parallax';
import HeroSection from "../components/HeroSection";
import AboutSection from "../components/AboutSection";
import ProjectsSection from "../components/ProjectsSection";

export default function Home() {
  
  // 1. FINALIZED FACTORS (from previous steps)
  // These define the scroll space reserved for each main component.
  const heroFactor = 1.05; 
  const aboutFactor = 0.8;  
  const projectsFactor = 0.9; 

  // 2. CALCULATED OFFSETS
  const heroOffset = 0;
  const aboutOffset = heroOffset + heroFactor; // 1.05
  const projectsOffset = aboutOffset + aboutFactor; // 1.85
  
  // Total pages needed for scrolling through all content.
  const totalPages = projectsOffset + projectsFactor + 0.1; // 2.85

  // 3. Helper for background colors (you can replace with images/textures)
  const bg_color_1 = '#5c6c80'; // Dark/Muted background for Hero area
  const bg_color_2 = '#61dafb'; // Brighter transition color
  const bg_color_3 = '#212529'; // Darker color for Projects

  return (
    <Parallax pages={totalPages} className="scroll-smooth" style={{ top: '0', left: '0' }}>
      
      {/* --------------------- BACKGROUND LAYERS (Slow Speeds) --------------------- */}

      {/* Layer 1: Background Color for Hero Section area (Offset 0 to 1.05) */}
      <ParallaxLayer 
        offset={0} 
        speed={0} 
        factor={aboutOffset} // Covers Hero area and up to the start of About
        style={{ backgroundColor: bg_color_1 }} 
      />
      
      {/* Layer 2: Background Color Transition (Covers About Section area) */}
      <ParallaxLayer 
        offset={aboutOffset} 
        speed={0} 
        factor={aboutFactor} 
        style={{ backgroundColor: bg_color_2 }} 
      />

      {/* Layer 3: Background Color for Projects Section area (Covers Projects) */}
      <ParallaxLayer 
        offset={projectsOffset} 
        speed={0} 
        factor={projectsFactor} 
        style={{ backgroundColor: bg_color_3 }} 
      />

      {/* Example: A slow-moving image/element spanning the first two sections */}
      <ParallaxLayer offset={0} speed={0.2} factor={projectsOffset} style={{ pointerEvents: 'none' }}>
        <div style={{ fontSize: '150px', marginLeft: '5%', color: 'rgba(255,255,255,0.1)' }}>{"{ ... }"}</div>
      </ParallaxLayer>


      {/* --------------------- CONTENT LAYERS (Offset = Start Position) --------------------- */}

      {/* 🦸 Hero Section - Content */}
      <ParallaxLayer offset={heroOffset} speed={0.5} factor={heroFactor}> 
        <HeroSection />
      </ParallaxLayer>

      {/* 👤 About Section - Content */}
      <ParallaxLayer offset={aboutOffset} speed={1.5} factor={aboutFactor}> 
        <AboutSection />
      </ParallaxLayer>

      {/* 🚀 Projects Section - Content */}
      <ParallaxLayer offset={projectsOffset} speed={0.5} factor={projectsFactor}> 
        <ProjectsSection />
      </ParallaxLayer>


      {/* --------------------- FOREGROUND LAYERS (Fast Speeds, placed within gaps) --------------------- */}

      {/* Example: Fast element between Hero (1.05) and About (1.85) */}
      <ParallaxLayer offset={aboutOffset + 0.1} speed={2.5} style={{ pointerEvents: 'none' }}>
        <div style={{ fontSize: '50px', marginLeft: '60%', color: 'rgba(0,0,0,0.1)' }}>// Full Stack</div>
      </ParallaxLayer>

      {/* Example: Fast element between About (1.85) and Projects (2.85) */}
      <ParallaxLayer offset={projectsOffset + 0.1} speed={2.0} style={{ pointerEvents: 'none' }}>
        <div style={{ fontSize: '70px', marginLeft: '10%', color: 'rgba(255,255,255,0.1)' }}>&lt;Code /&gt;</div>
      </ParallaxLayer>

    </Parallax>
  );
}